body, html {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
    background-color: #F4F4F4;
    color: #333;
    height: 100%;
}
*{
    box-sizing: border-box;  
}
#root{
    height: 100%;
}
.container {
    max-width: 414px;
    margin: 0 auto;
    background-color: white;
    height: 100%;
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
}
header {
    background-color: #F9E3E0;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header-title {
    font-size: 18px;
    font-weight: bold;
}
.header-actions {
    display: flex;
    gap: 15px;
}
.icon {
    width: 24px;
    height: 24px;
}
.note-list {
    flex-grow: 1;
    overflow-y: auto;
    padding: 10px 20px;
}
.note-item {
    background-color: #FFF8F7;
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
}
.note-content {
    flex-grow: 1;
    font-size: 16px;
    font-weight: bold;
    margin-right: 10px;
}
.note-meta {
    font-size: 12px;
    color: #888;
    margin-top: 5px;
}
.qr-code {
    width: 60px;
    height: 60px;
    background-color: #ddd;
    flex-shrink: 0;
}
.add-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 56px;
    height: 56px;
    background-color: #F9776F;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 10px rgba(249, 119, 111, 0.3);
}

.edit-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;
    background: #fff;
}
.edit-section {
    margin-bottom: 20px;
    
}

.edit-textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
    height: 150px;
    resize: vertical;
    
}
.image-upload-section {
    display: flex;
    align-items: center;
    gap: 10px;
    border: 1px dashed #ccc;
    padding: 10px;
    border-radius: 4px;
    cursor: pointer;
}
.image-upload-icon {
    width: 24px;
    height: 24px;
    fill: #F9776F;
}
.image-upload-text {
    font-size: 14px;
    color: #666;
}
.location-section {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.location-info {
    display: flex;
    align-items: center;
    gap: 10px;
}
.location-icon {
    width: 24px;
    height: 24px;
    fill: #F9776F;
}
#map {
    width: 100%;
    height: 200px;
    border-radius: 4px;
    overflow: hidden;
}
.save-button {
    background-color: #F9776F;
    color: white;
    border: none;
    padding: 12px;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    margin-top: auto;
}
#imageUpload {
    display: none;
}
.location-note-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    margin-top: 10px;
}
.image-preview-section {
    margin-top: 10px;
    text-align: center;
  }
  
  .preview-image {
    max-width: 100%;
    max-height: 200px;
    object-fit: contain;
  }